@import url(https://fonts.googleapis.com/css2?family=PT+Sans+Caption:wght@400;700&family=PT+Sans:ital,wght@0,400;0,700;1,400&display=swap);
:root {
  --font-family: "PT Sans Caption", sans-serif;

  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);

  --color-bg: #f2f2f2;
  --color-bg-2: #f0ebdb;

  --color-text: #25282a;
  --color-subtext: #ff8a71;

  /* 12px, 16px, 20px, 52px, 62px */
  --font-sml: 0.785rem;
  --font-reg: 1rem;
  --font-med: 1.25rem;
  --font-lg: 2rem;
  --font-xl: 3rem;
  --font-xxl: 3.975rem;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin-bottom: 2rem;
  padding: 1rem 0;
  text-align: right;
  border-bottom: 2px solid var(--color-text);
  z-index: 2000;
}

.header a {
  font-weight: 700;
}
.header a:hover {
  color: purple;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem 0;
  text-align: left;
  border-top: 2px solid var(--color-text);
  z-index: 2000;
}

.footer svg {
  font-size: 1.5rem;
}

.footer svg.scrolling path {
  fill: crimson;
}

