.hero {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  /* order: 8px solid blue; */
}
.wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  /* border: 4px solid yellow; */
}
.hero__name {
  position: absolute;
  left: 0;
  top: 20rem;
  flex-direction: column;
  justify-content: center;
}
.name-social {
  margin-top: 5rem;
  opacity: 0;
  transform: translateY(1rem);
  animation: fadeInUp 1.5s ease 2s forwards;
}
.social--icon svg {
  font-size: 1.5rem;
  margin-right: 1rem;
}
.social--icon svg path {
  transition: all 0.2s ease-in-out;
}
/* .social--icon:hover.social--icon svg {
  font-size: 1.7rem;
} */
.social--icon:hover.social--icon svg path {
  fill: rgb(99, 30, 155);
}
.hero__competencies {
  position: absolute;
  top: 10rem;
  right: 0;
  z-index: 9000;
}
.hero__works,
.hero__works-two {
  max-width: 60%;
  position: absolute;
  top: 10rem;
  right: 0;
  opacity: 0;
  transform: translateY(40px);
}

/* Animations */

@keyframes typewriter {
  to {
    left: 100%;
  }
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
    z-index: 10000;
  }
}

@keyframes fadeOut {
  to {
    transform: translateY(-10px);
    opacity: 0;
    z-index: 8000;
  }
}

.fade-out {
  animation: fadeOut 0.4s ease forwards;
}
.fade-in {
  animation: fadeInUp 0.5s ease 0.2s forwards;
}

/* typewriter */
.name-typewriter {
  position: relative;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-bottom: 0.8rem;
}

.name-typewriter::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.name-typewriter::before {
  background: var(--color-bg);
  animation: typewriter 2s ease forwards;
}

.name-subtitle {
  font-size: var(--font-med);
  font-weight: 400;
  color: var(--color-text);
  opacity: 0;
  transform: translateY(1rem);
  animation: fadeInUp 1.5s ease 2s forwards;
}

/* Media queries */
@media screen and (max-width: 599px) {
  .wrapper {
    left: 1rem;
    right: 1rem;
  }
  .hero__name {
    width: 100%;
    top: 5rem;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .name-social {
    margin-top: 0;
  }
  .hero__competencies {
    top: 15rem;
  }
  .hero__works,
  .hero__works-two {
    max-width: 100%;
    top: 15rem;
    left: 0;
  }
}

.info {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  /* order: 8px solid blue; */
}
.info__wrapper {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  flex-wrap: nowrap;
  grid-gap: 7rem;
  gap: 7rem;
  /* border: 4px solid yellow; */
}

.info__greeting {
  position: absolute;
  top: 20rem;
  left: 15rem;
  font-size: var(--font-xl);
}

.info__img {
  position: absolute;
  background-position: center;
  background-size: 95%;
  background-repeat: no-repeat;
}

.info__img.one {
  width: 500px;
  height: 530px;
  top: 10rem;
  left: calc(100vw / 2);
  transform: translateX(-50%);
}
.info__img.two {
  width: 600px;
  height: 430px;
  top: 10rem;
  left: calc(100vw / 3);
  opacity: 0;
}

.info__greeting > h1 {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /* transform: translateY(-10px); */
  opacity: 0;
}

.info__text {
  position: absolute;
  width: 30%;
  top: 10rem;
  left: 15rem;
  opacity: 0;
}

/* Animations */

@keyframes fadeInOut {
  0% {
    /* transform: translateY(-10px); */
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  50% {
    opacity: 1;
    /* transform: translateY(0); */
  }
  70% {
    opacity: 1;
  }
  100% {
    /* transform: translateY(10px); */
    opacity: 0;
  }
}
@keyframes fadeIn {
  to {
    /* transform: translateY(0px); */
    opacity: 1;
  }
}

.info__greeting h1:nth-child(1) {
  animation: fadeInOut 0.6s ease-in 0.2s forwards;
}
.info__greeting h1:nth-child(2) {
  animation: fadeInOut 0.6s ease-in 0.9s forwards;
}
.info__greeting h1:nth-child(3) {
  animation: fadeInOut 0.6s ease-in 1.6s forwards;
}
.info__greeting h1:nth-child(4) {
  animation: fadeInOut 0.6s ease-in 2.3s forwards;
}
.info__greeting h1:nth-child(5) {
  animation: fadeIn 0.6s ease-in 2.9s forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
    z-index: 10000;
  }
}

@keyframes fadeOut {
  to {
    transform: translateY(-40px);
    opacity: 0;
    z-index: 8000;
  }
}

.fade-out {
  animation: fadeOut 0.4s ease forwards;
}
.fade-in {
  animation: fadeInUp 0.5s ease 0.2s forwards;
}

@media screen and (max-width: 599px) {
  .info__wrapper {
    left: 1rem;
    right: 1rem;
  }
  .info__greeting {
    top: 65vh;
    left: 0;
    font-size: var(--font-xl);
  }
  .info__greeting > h1 {
    color: #fff;
  }
  .info__img.one {
    top: 16vh;
  }
  .info__img.two {
    top: 6vh;
    left: -10rem;
  }
  .info__text {
    width: 100%;
    top: 65vh;
    left: 0;
    font-size: var(--font-med);
  }
}

/* global styles */

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  font-family: "PT Sans", sans-serif;
  background: var(--color-bg);
  margin: 0;
  padding: 0;
  font-size: var(--font-med);
  font-weight: 400;
  line-height: 1.3;
  color: var(--color-text);
  overflow-x: hidden;
  transition: background-color 2s ease-in;
  /* border: 2px solid red; */
}

.app {
  position: relative;
  height: calc(800px + 100vh);
  /* border: 3px solid green; */
}

@media screen and (max-width: 599px) {
  body {
    font-size: var(--font-reg);
  }
}

/* text */

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "PT Sans Caption", sans-serif;
  margin: 0;
  font-weight: 700;
}

a,
a:visited,
a:hover {
  color: unset;
  text-decoration: none;
}

/* spacing */

.container {
  max-width: 80.375rem;
  margin: 0 auto;
}
.container--pdall {
  padding-top: 4.375rem;
  padding-right: 1.5rem;
  padding-bottom: 4.375rem;
  padding-left: 1.5rem;
}
.container--pdY {
  padding-top: 4.375rem;
  padding-bottom: 4.375rem;
}
.container--pdX {
  padding-right: 1.5rem;
  padding-left: 1.5rem;
}

/* flexbox */

.flex {
  display: flex;
  flex-wrap: wrap;
}
.jc-sb {
  justify-content: space-between;
}
.jc-sa {
  justify-content: space-around;
}
.jc-c {
  justify-content: center;
}
.jc-fe {
  justify-content: flex-end;
}
.ai-c {
  align-items: center;
}
.ai-fs {
  align-items: flex-start;
}
.ai-fe {
  align-items: flex-end;
}

/* visibility */

@media screen and (max-width: 1050px) {
  .hide-for-desktop {
    display: none;
  }
}
@media screen and (max-width: 650px) {
  .hide-for-mobile {
    display: none;
  }
}

